import React from "react";
import SquareEelement from "./SquareEelement";
import css from "./Square.module.css";

const square = ({
  maze,
  size,
  start,
  finish,
  onSquareClick,
  onStartClick,
  onNextClick,
  onReloadClick,
  baseSquare,
  levelStage,
  isWin
}) => (
  <>
    {isWin ? (
      <button type="button" onClick={onNextClick} className={css.button}>
        Next level
      </button>
    ) : (
      <button type="button" onClick={onStartClick} className={css.button}>
        {levelStage < 4 ? "Start" : "Try again"}
      </button>
    )}

    <div
      className={css.square}
      style={{ width: `${baseSquare}px`, height: `${baseSquare}px` }}
      id="feald"
    >
      {levelStage < 3 || levelStage === 4 ? (
        <div
          className={css.cover}
          style={{ width: `${baseSquare}px`, height: `${baseSquare}px` }}
        ></div>
      ) : null}
      {maze.map(({ x, y }) => (
        <SquareEelement
          key={x + y}
          x={x}
          y={y}
          start={start}
          finish={finish}
          onClick={onSquareClick}
          baseSquare={baseSquare}
          size={size}
          levelStage={levelStage}
          isWin={isWin}
        />
      ))}
    </div>
    {levelStage === 1 && (
      <button className={css.button} onClick={onReloadClick}>
        Start from level 1
      </button>
    )}
  </>
);

export default square;
