import React, { Component } from "react";
import Square from "./components/Square";
import initInfo from "./data/initialFile";
import stepsVariants from "./data/stepsVariants";
import Steps from "./components/Steps";
import shortid from "shortid";
import RulesModal from "./components/RulesModal";
import * as localStorage from "./utils/localStorage";
import levelOptions from "./data/levelOptions";
import Options from "./components/Options";

export default class App extends Component {
  state = {
    modalIsOpen: true,
    baseSquare: 300,
    maze: [],
    size: levelOptions.size.small,
    stepsAmount: levelOptions.steps.easy,
    basicSpeed: levelOptions.speed.slow,
    start: {},
    finish: {},
    mazeLetters: [],
    steps: [],
    gameStage: 1,
    levelStage: 1,
    isWin: false
  };

  componentDidMount() {
    this.createMazeLetters();

    const modalAlwaisOpenLS = localStorage.load("alwaisShowModal");
    const gameStageLS = localStorage.load("gameStage");

    if (modalAlwaisOpenLS === "off") {
      this.setState({
        modalIsOpen: false
      });
    }

    if (gameStageLS) {
      this.setState({ gameStage: gameStageLS });
    }
  }

  componentDidUpdate(prevProps, prevState) {
    if (prevState.mazeLetters !== this.state.mazeLetters) {
      this.randomStartPoint();
      this.createMazeArr();
    }

    if (prevState.size !== this.state.size) {
      this.createMazeLetters();
    }

    if (prevState.gameStage !== this.state.gameStage) {
      localStorage.save("gameStage", this.state.gameStage);
      this.levelGenerator();
    }
  }

  createMazeArr = () => {
    const { size, mazeLetters } = this.state;
    const initMaze = mazeLetters
      .map(letter => {
        let initialArr = [];
        for (let i = 1; i <= size; i += 1) {
          initialArr = [...initialArr, { x: i, y: letter }];
        }

        return initialArr;
      })
      .flat();
    this.setState({ maze: [...initMaze] });
  };

  createMazeLetters = () => {
    const { size } = this.state;
    const mazeLetters = initInfo.slice(0, size);
    this.setState({ mazeLetters });
  };

  randomStartPoint = () => {
    const { size, mazeLetters } = this.state;
    const randomIndex = Math.floor(Math.random() * Math.floor(size));
    const y = mazeLetters[randomIndex];
    const x = Math.floor(Math.random() * Math.floor(size)) + 1;
    this.setState({ start: { x, y } });
  };

  mazeSteps = () => {
    const { start, basicSpeed, stepsAmount } = this.state;

    let actualCoords = { ...start };
    let tempSteps = [];
    let counter = 0;

    for (let i = 0; i < stepsAmount; i += 1) {
      let actualSteps = [...stepsVariants];

      actualSteps = this.doActualStepsVariant(actualSteps, actualCoords);
      const step = {
        arrow: this.chooseStep(actualSteps),
        id: shortid.generate()
      };
      tempSteps = [...tempSteps, step];
      actualCoords = this.changeActualCoords(step, actualCoords);
    }
    this.setState({ finish: actualCoords });

    const interval = setInterval(() => {
      counter += 1;
      counter > stepsAmount
        ? clearInterval(interval)
        : this.setState({ steps: tempSteps.slice(0, counter) });
    }, basicSpeed);

    setTimeout(() => {
      this.setState({ steps: [], levelStage: 3 });
    }, basicSpeed * stepsAmount + basicSpeed * 2);
  };

  doActualStepsVariant = (actualSteps, actualCoords) => {
    const { size, mazeLetters } = this.state;
    const lastY = mazeLetters[size - 1];

    if (actualCoords.x === 1) {
      actualSteps = actualSteps.filter(step => step !== "top");
    }

    if (actualCoords.x === size) {
      actualSteps = actualSteps.filter(step => step !== "bot");
    }

    if (actualCoords.y === "A") {
      actualSteps = actualSteps.filter(step => step !== "left");
    }

    if (actualCoords.y === lastY) {
      actualSteps = actualSteps.filter(step => step !== "right");
    }

    return actualSteps;
  };

  chooseStep = actualSteps => {
    const randomIndex = Math.floor(
      Math.random() * Math.floor(actualSteps.length)
    );
    return actualSteps[randomIndex];
  };

  changeActualCoords = (step, actualCoords) => {
    const { mazeLetters } = this.state;
    const indexY = mazeLetters.indexOf(actualCoords.y);

    switch (step.arrow) {
      case "top":
        actualCoords.x = actualCoords.x - 1;
        break;
      case "bot":
        actualCoords.x = actualCoords.x + 1;
        break;
      case "right":
        actualCoords.y = mazeLetters[indexY + 1];
        break;
      case "left":
        actualCoords.y = mazeLetters[indexY - 1];
        break;
      default:
        return actualCoords;
    }
    return actualCoords;
  };

  onSquareClick = e => {
    const { finish } = this.state;

    this.setState({ levelStage: 4 });

    if (
      Number(e.target.dataset.x) === finish.x &&
      e.target.dataset.y === finish.y
    ) {
      this.setState({ isWin: true });
      e.target.classList.add("win");
    } else {
      e.target.classList.add("lose");
    }
  };

  onStartClick = () => {
    const { levelStage } = this.state;

    if (levelStage === 2) return;

    const steps = document.querySelector("#steps");
    steps.scrollIntoView({ block: "end", behavior: "smooth" });

    this.setState({ levelStage: 2 });
    this.mazeSteps();

    if (levelStage === 4) {
      const feald = document.querySelector("#feald");
      const fealdChildren = Array.from(feald.children);

      fealdChildren.forEach(el => {
        if (el.classList.contains("win")) {
          el.classList.remove("win");
        }

        if (el.classList.contains("lose")) {
          el.classList.remove("lose");
        }
      });
    }
  };

  onNextClick = () => {
    this.setState({ isWin: false, levelStage: 1 });
    this.setState((state, props) => {
      return { gameStage: state.gameStage + 1 };
    });
    const feald = document.querySelector("#feald");
    const fealdChildren = Array.from(feald.children);

    fealdChildren.forEach(el => {
      if (el.classList.contains("win")) {
        el.classList.remove("win");
      }

      if (el.classList.contains("lose")) {
        el.classList.remove("lose");
      }
    });
  };

  onModalCloseClick = () => {
    this.setState({
      modalIsOpen: false
    });
    const input = document.querySelector("#notOpen");

    if (input.checked) {
      localStorage.save("alwaisShowModal", "off");
    }
  };

  onReloadClick = () => {
    this.setState({ gameStage: 1 });
  };

  handelChange = e => {
    this.setState({ [e.name]: e });
  };

  levelGenerator = () => {
    const { gameStage } = this.state;
    const { size, speed, steps } = levelOptions;

    switch (gameStage) {
      case 1:
        this.setState({
          size: levelOptions.size.small,
          stepsAmount: levelOptions.steps.easy,
          basicSpeed: levelOptions.speed.slow
        });
        break;
      case 2:
        this.setState({
          size: levelOptions.size.small,
          stepsAmount: levelOptions.steps.easy,
          basicSpeed: speed.normal
        });
        break;
      case 3:
        this.setState({
          size: levelOptions.size.small,
          basicSpeed: speed.normal,
          stepsAmount: steps.normal
        });
        break;
      case 4:
        this.setState({
          size: size.normal,
          basicSpeed: speed.normal,
          stepsAmount: steps.normal
        });
        break;
      case 5:
        this.setState({
          size: size.normal,
          basicSpeed: speed.fast,
          stepsAmount: steps.normal
        });
        break;
      case 6:
        this.setState({
          size: size.large,
          basicSpeed: speed.fast,
          stepsAmount: steps.normal
        });
        break;
      case 7:
        this.setState({
          size: size.large,
          basicSpeed: speed.fast,
          stepsAmount: steps.hard
        });
        break;
      case 8:
        this.setState({
          size: size.insane,
          basicSpeed: speed.fast,
          stepsAmount: steps.hard
        });
        break;
      case 9:
        this.setState({
          size: size.insane,
          basicSpeed: speed.insane,
          stepsAmount: steps.hard
        });
        break;
    }
  };

  render() {
    const {
      maze,
      size,
      start,
      finish,
      steps,
      baseSquare,
      modalIsOpen,
      levelStage,
      gameStage,
      isWin,
      stepsAmount
    } = this.state;

    return (
      <>
        {modalIsOpen && levelStage < 2 && (
          <RulesModal onClick={this.onModalCloseClick} />
        )}

        <div className="wrapper">
          {/* <Options
            onChange={this.handelChange}
            sizeValue={size}
            speedValue={basicSpeed}
            stepValue={stepsAmount}
          /> */}
          <h2>Level {gameStage}</h2>
          <Square
            baseSquare={baseSquare}
            maze={maze}
            size={size}
            start={start}
            finish={finish}
            onSquareClick={this.onSquareClick}
            onStartClick={this.onStartClick}
            onNextClick={this.onNextClick}
            onReloadClick={this.onReloadClick}
            levelStage={levelStage}
            isWin={isWin}
          />
          <Steps steps={steps} stepsAmount={stepsAmount} />
        </div>
      </>
    );
  }
}
